import { DialogActions, DialogContent, DialogProps } from "@mui/material"
import { Dialog } from "../Dialog"
import {
  Button,
  InputBox,
  InputGroup,
  InputWrapper,
  Radio,
  RadioGroup,
  RadioGroupWrapper
} from "./styles"
import { useContext } from "react"
import { ShoppingCartContext } from "../../../contexts/ShoppingCart/ShoppingCart.context"
import { BsWhatsapp } from "react-icons/bs"
import FormControlLabel from "@mui/material/FormControlLabel"
import Checkbox from "@mui/material/Checkbox"

type AdditionalnformationDialogProps = DialogProps & {
  dispatch?: () => void
}

export function AdditionalnformationDialog({
  open,
  onClose,
  dispatch
}: AdditionalnformationDialogProps) {
  const { additionalInformation, updateAdditionalInformation } =
    useContext(ShoppingCartContext)
  const {
    name,
    street,
    number,
    neighborhood,
    block,
    adjunct,
    phoneNumber,
    delivery,
    payment
  } = additionalInformation

  return (
    <Dialog
      title="Informações adicionais"
      fullWidth={true}
      maxWidth={"sm"}
      open={open}
      onClose={onClose}
    >
      <DialogContent>
        <InputBox className="mb-2">
          <InputWrapper>
            <label htmlFor="name">
              Nome<span>*</span>
            </label>
            <input
              id="name"
              type="text"
              value={name}
              placeholder="Digite seu nome"
              onChange={(e) => {
                const name = e.target.value
                if (!name) e.target.required = true
                updateAdditionalInformation({
                  ...additionalInformation,
                  name
                })
              }}
            />
          </InputWrapper>
          <span className="error">O campo nome é obrigatório!</span>
        </InputBox>

        <InputBox className="mb-2">
          <InputWrapper>
            <label htmlFor="street">
              Endereço<span>*</span>
            </label>
            <input
              id="street"
              type="text"
              value={street}
              placeholder="Rua: José Francisco de Souza"
              onChange={(e) => {
                const street = e.target.value
                if (!street) e.target.required = true
                updateAdditionalInformation({
                  ...additionalInformation,
                  street
                })
              }}
            />
          </InputWrapper>
          <span className="error">O campo endereço é obrigatório!</span>
        </InputBox>

        <InputGroup>
          <InputBox>
            <InputWrapper>
              <label htmlFor="number">Nº</label>
              <input
                id="number"
                type="text"
                value={number}
                placeholder="42"
                onChange={(e) => {
                  const number = e.target.value
                  updateAdditionalInformation({
                    ...additionalInformation,
                    number
                  })
                }}
              />
            </InputWrapper>
          </InputBox>

          <InputBox>
            <InputWrapper>
              <label htmlFor="neighborhood">Bairro</label>
              <input
                id="neighborhood"
                type="text"
                value={neighborhood}
                placeholder="Digite o bairro"
                onChange={(e) => {
                  const neighborhood = e.target.value
                  updateAdditionalInformation({
                    ...additionalInformation,
                    neighborhood
                  })
                }}
              />
            </InputWrapper>
          </InputBox>
        </InputGroup>

        <InputGroup>
          <InputBox>
            <InputWrapper>
              <label htmlFor="block">Quadra</label>
              <input
                id="block"
                type="text"
                value={block}
                placeholder="Digite a quadra"
                onChange={(e) => {
                  const block = e.target.value
                  updateAdditionalInformation({
                    ...additionalInformation,
                    block
                  })
                }}
              />
            </InputWrapper>
          </InputBox>

          <InputBox>
            <InputWrapper>
              <label htmlFor="adjunct">Complemento</label>
              <input
                id="adjunct"
                type="text"
                value={adjunct}
                placeholder="Digite um ponto referência"
                onChange={(e) => {
                  const adjunct = e.target.value
                  updateAdditionalInformation({
                    ...additionalInformation,
                    adjunct
                  })
                }}
              />
            </InputWrapper>
          </InputBox>
        </InputGroup>

        <InputGroup>
          <InputBox>
            <InputWrapper>
              <label htmlFor="phone">Telefone</label>
              <input
                id="phone"
                type="tel"
                value={phoneNumber}
                onChange={(e) => {
                  const phoneNumber = e.target.value
                    .toString()
                    .replace(/[^\d]/g, "")
                    .replace(/(\d{2})(\d{1})(\d{4})(\d{4})/, "($1) $2$3-$4")

                  if (phoneNumber.length <= 15)
                    updateAdditionalInformation({
                      ...additionalInformation,
                      phoneNumber
                    })
                }}
                placeholder="(99) 99999-9999"
              />
            </InputWrapper>
          </InputBox>

          <FormControlLabel
            control={
              <Checkbox
                checked={delivery}
                onChange={(e) => {
                  const delivery = e.target.checked
                  updateAdditionalInformation({
                    ...additionalInformation,
                    delivery
                  })
                }}
              />
            }
            label="Quer que realizemos a entrega?"
          />
        </InputGroup>

        <RadioGroupWrapper>
          <span>Selecione um método de pagamento</span>
          <RadioGroup>
            <Radio>
              <input
                type="radio"
                name="payment"
                value="money"
                onChange={(e) => {
                  const payment = e.target.value
                  updateAdditionalInformation({
                    ...additionalInformation,
                    payment
                  })
                }}
                checked={payment === "money"}
              />
              <span className="label">
                <span className="material-icons-outlined">local_atm</span>
                Dinheiro
              </span>
            </Radio>
            <Radio>
              <input
                type="radio"
                name="payment"
                value="pix"
                onChange={(e) => {
                  const payment = e.target.value
                  updateAdditionalInformation({
                    ...additionalInformation,
                    payment
                  })
                }}
                checked={payment === "pix"}
              />
              <span className="label">
                <span className="material-icons-outlined">pix</span>
                Pix
              </span>
            </Radio>
            <Radio>
              <input
                type="radio"
                name="payment"
                value="card"
                onChange={(e) => {
                  const payment = e.target.value
                  updateAdditionalInformation({
                    ...additionalInformation,
                    payment
                  })
                }}
                checked={payment === "card"}
              />
              <span className="label">
                <span className="material-icons-outlined">credit_card</span>
                Cartão
              </span>
            </Radio>
          </RadioGroup>
        </RadioGroupWrapper>
      </DialogContent>
      <DialogActions>
        <Button onClick={() => dispatch!()} disabled={!name || !street}>
          <BsWhatsapp /> Finalizar compra
        </Button>
      </DialogActions>
    </Dialog>
  )
}
