import styled from "styled-components"

export const Container = styled.div`
  h3 {
    font-size: 18px;
    text-align: center;
    margin-bottom: 16px;
  }
`

export const Table = styled.table`
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 24px;

  thead {
    tr {
      border-bottom: 1px solid #dee2e6;
      th {
        text-align: left;

        &.action {
          text-align: center;
        }
      }
    }
  }

  tbody {
    td {
      vertical-align: middle;
      padding-block: 8px;
      &.id {
        width: 28px;
      }
      &.img {
        width: 36px;
      }
      &.price {
        width: 100px;
      }
      &.qtde {
        width: 60px;
        @media (max-width: 560px) {
          width: 48px;
          input {
            width: inherit;
            font-size: 18px;
          }
        }
      }
      &.action {
        width: 60px;
        text-align: center;

        button {
          background-color: transparent;
          border: none;
          color: red;
          line-height: 0;
          padding: 2px;
          border-radius: 2px;
          transition: all 300ms ease;
          cursor: pointer;

          &:hover,
          &:focus {
            background-color: red;
            color: #fff;
          }
        }
      }

      input {
        max-width: 56px;
        font-size: 20px;
      }
      img {
        max-height: 32px;
        aspect-ratio: 1;
        border-radius: 50%;
        object-fit: cover;
      }
    }
  }

  @media (max-width: 560px) {
    td,
    th {
      font-size: 12px;
    }
  }
`

export const Button = styled.button`
  display: flex;
  margin: 0 16px;
  align-items: center;
  justify-content: center;
  gap: 8px;
  background-color: #dc143c;
  color: #ffffff;
  border: none;
  padding: 12px 16px;
  border-radius: 4px;
  width: 100%;
  font-size: 18px;
  cursor: pointer;
  transition: background-color 250ms ease;
  margin-bottom: 24px;

  &:hover {
    background-color: #ee284f;
  }
`

export const Total = styled.span`
  padding: 8px;
  display: block;
  text-align: right;
  font-size: 20px;
  margin-bottom: 16px;
`
