// import { HeaderContainer } from "./styles"

import { Card } from "../Card"
import { Cards, CardsGroups } from "./styles"

type ProductGroupProps = {
  productGroup: ProductGroupType
}

export function ProductGroup({ productGroup }: ProductGroupProps) {
  return (
    <CardsGroups>
      <h2>{productGroup.name}</h2>
      <Cards>
        {productGroup.items.map((product, index) => (
          <Card
            group={productGroup.name}
            product={product}
            key={product.id + index}
          />
        ))}
      </Cards>
    </CardsGroups>
  )
}
