import styled from "styled-components"

export const Button = styled.button`
  display: flex;
  margin: 0 16px;
  align-items: center;
  justify-content: center;
  gap: 8px;
  color: #ffffff;
  border: none;
  padding: 12px 16px;
  border-radius: 4px;
  width: 100%;
  font-size: 18px;
  cursor: pointer;
  transition: background-color 250ms ease;
  margin-bottom: 24px;

  &:not(:disabled) {
    background-color: #dc143c;
    &:hover {
      background-color: #ee284f;
    }
  }

  &:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
  }
`
export const Terms = styled.div`
  overflow-y: auto;
  height: 180px;

  p {
    margin-bottom: 16px;
  }

  ol {
    padding-inline-start: 40px;
  }
`

export const Checkbox = styled.label`
  margin-top: 24px;
  display: flex;
  align-items: center;
  gap: 12px;
  cursor: pointer;

  input {
    scale: 1.5;
  }

  label {
    display: flex;
    align-items: center;
    gap: 2px;
    font-weight: bold;
    color: #333;
    user-select: none;
  }
`
