import { DialogActions, DialogContent, DialogProps } from "@mui/material"
import { Dialog } from "../Dialog"
import { Table, Button, Container, Total } from "./styles"
import { useContext } from "react"
import { ShoppingCartContext } from "../../../contexts/ShoppingCart/ShoppingCart.context"

type ShoppingCartDialogProps = DialogProps & {
  dispatch?: () => void
}

export function ShoppingCartDialog({
  open,
  onClose,
  dispatch
}: ShoppingCartDialogProps) {
  const { products, updateProducts, total } = useContext(ShoppingCartContext)

  const shoppingCartFiltered = products?.filter((productGroup) => {
    const filtered = productGroup.items.filter((product) => product.disabled)
    return filtered.length > 0 ? filtered : false
  })

  const shoppingCart = shoppingCartFiltered
    ?.map((productGroup) => ({
      ...productGroup,
      items: productGroup.items.filter((product) => product.disabled)
    }))
    

  return (
    <Dialog
      title="Carrinho de compras"
      fullWidth={true}
      maxWidth={"sm"}
      open={open}
      onClose={onClose}
    >
      <DialogContent>
        {shoppingCart?.length > 0 ? (
          shoppingCart.map((productGroup) => (
            <Container key={productGroup.name}>
              <h3>{productGroup.name}</h3>
              <Table>
                <thead>
                  <tr>
                    <th>id</th>
                    <th colSpan={2}>nome</th>
                    <th>preço</th>
                    <th>qtde</th>
                    <th className="action">excluir</th>
                  </tr>
                </thead>
                <tbody>
                  {productGroup.items.map((product, index) => (
                    <tr key={product.id + index}>
                      <td className="id">{product.id}</td>
                      <td className="img">
                        <img
                          src={product.img}
                          alt={"Imagem ilustrativa do produto " + product.name}
                        />
                      </td>
                      <td className="product">{product.name}</td>
                      <td className="price">
                        {product.price.toLocaleString("pt-BR", {
                          style: "currency",
                          currency: "BRL"
                        })}
                      </td>
                      <td className="qtde">
                        <input
                          type="number"
                          value={product.quantity}
                          onChange={({ target }) => {
                            const newProducts = products.map(
                              (productGroupMap) => {
                                let newProductGroup = productGroupMap
                                if (
                                  productGroupMap.name === productGroup.name
                                ) {
                                  newProductGroup.items =
                                    productGroupMap.items.map((item) => {
                                      let newItem = item
                                      if (item.id === product.id) {
                                        newItem = {
                                          ...item,
                                          quantity: target.value
                                        }
                                      }
                                      return newItem
                                    })
                                }
                                return productGroupMap
                              }
                            )

                            updateProducts(newProducts)
                          }}
                          placeholder="0"
                        />
                      </td>
                      <td className="action">
                        <button
                          onClick={() => {
                            const newProducts = products.map(
                              (productGroupMap) => {
                                let newProductGroup = productGroupMap
                                if (
                                  productGroupMap.name === productGroup.name
                                ) {
                                  newProductGroup.items =
                                    productGroupMap.items.map((item) => {
                                      let newItem = item
                                      if (item.id === product.id) {
                                        newItem = {
                                          ...item,
                                          disabled: false,
                                          quantity: "0",
                                          price: +product.prices[0].value
                                        }
                                      }
                                      return newItem
                                    })
                                }
                                return productGroupMap
                              }
                            )

                            updateProducts(newProducts)
                          }}
                        >
                          <span className="material-icons-outlined">
                            delete
                          </span>
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </Table>
            </Container>
          ))
        ) : (
          <>Não há nenhum produto em seu carrinho de compras.</>
        )}
      </DialogContent>
      {shoppingCart?.length > 0 ? (
        <>
          <Total>
            Total:{" "}
            <strong>
              {total.toLocaleString("pt-BR", {
                style: "currency",
                currency: "BRL"
              })}
            </strong>
          </Total>
          <DialogActions className="p-3">
            <Button onClick={() => dispatch!()}>Próximo passo</Button>
          </DialogActions>
        </>
      ) : null}
    </Dialog>
  )
}
