import { WrapperSocialMedia } from "./styles"
import { BsWhatsapp, BsInstagram } from "react-icons/bs"
import { FaFacebookF } from "react-icons/fa"

const { REACT_APP_FACEBOOK, REACT_APP_WHATSAPP, REACT_APP_INSTAGRAM } =
  process.env

export function SocialMedia() {
  return (
    <WrapperSocialMedia>
      <ul>
        <li>
          <a href={REACT_APP_WHATSAPP} target="_blank" rel="noreferrer">
            <BsWhatsapp />{" "}
          </a>
        </li>
        <li>
          <a href={REACT_APP_FACEBOOK} target="_blank" rel="noreferrer">
            <FaFacebookF />{" "}
          </a>
        </li>
        <li>
          <a href={REACT_APP_INSTAGRAM} target="_blank" rel="noreferrer">
            <BsInstagram />{" "}
          </a>
        </li>
      </ul>
    </WrapperSocialMedia>
  )
}
