import { useContext, useState } from "react"
import {
  AdditionalnformationDialog,
  Header,
  NoBondDialog,
  ProductGroup,
  ShoppingCartDialog,
  TermsAndConditionsDialog
} from "../../components/organisms"
import { ShoppingCartContext } from "../../contexts/ShoppingCart/ShoppingCart.context"
import { Main } from "./styles"
import { SocialMedia } from "../../components/molecules"

export default function Home() {
  const { products: data, sendMessage } = useContext(ShoppingCartContext)
  const [showShoppingCartDialog, setShowShoppingCartDialog] = useState(false)
  const [showAdditionalnformationDialog, setShowAdditionalnformationDialog] =
    useState(false)
  const [showTermsAndConditionsDialog, setShowTermsAndConditionsDialog] =
    useState<boolean>(() => {
      try {
        const terms = localStorage.getItem("terms")!
        return JSON.parse(terms) || false
      } catch {
        return false
      }
    })
  const [showNoBondDialog, setShowNoBondDialog] = useState<boolean>(() => {
    try {
      const noBond = localStorage.getItem("noBond")!
      return JSON.parse(noBond) || false
    } catch {
      return false
    }
  })

  return (
    <div>
      <Header setShowShoppingCartDialog={setShowShoppingCartDialog} />
      <Main>
        {data
          ? data.map((productGroup, index) => (
              <ProductGroup key={index + 1} productGroup={productGroup} />
            ))
          : null}
      </Main>
      <NoBondDialog
        open={!showNoBondDialog}
        dispatch={() => setShowNoBondDialog(true)}
        onClose={() => setShowNoBondDialog(true)}
      />
      <TermsAndConditionsDialog
        open={!showTermsAndConditionsDialog && showNoBondDialog}
        dispatch={() => setShowTermsAndConditionsDialog(true)}
        onClose={() => setShowTermsAndConditionsDialog(true)}
      />
      <ShoppingCartDialog
        dispatch={() => {
          setShowShoppingCartDialog(false)
          setShowAdditionalnformationDialog(true)
        }}
        open={showShoppingCartDialog}
        onClose={() => setShowShoppingCartDialog(false)}
      />
      <AdditionalnformationDialog
        open={showAdditionalnformationDialog}
        dispatch={() => sendMessage()}
        onClose={() => setShowAdditionalnformationDialog(false)}
      />
      <SocialMedia />
    </div>
  )
}
