import { Dialog as DialogMUI, DialogTitle, DialogProps } from "@mui/material"

import IconButton from "@mui/material/IconButton"

type Props = DialogProps & {
  title?: string
}

export function Dialog(props: Props) {
  return (
    <DialogMUI {...props}>
      <DialogTitle>
        {props.title}
        {props.onClose ? (
          <IconButton
            aria-label="close"
            onClick={() => props.onClose!({}, "backdropClick")}
            sx={{
              position: "absolute",
              right: 8,
              top: 8,
              color: "red"
            }}
          >
            <span className="material-icons-outlined">close</span>
          </IconButton>
        ) : null}
      </DialogTitle>
      {props.children}
    </DialogMUI>
  )
}
