import { useContext } from "react"
import {
  CardButton,
  CardContainer,
  Label,
  List,
  ListItem,
  Nav,
  Price,
  Prices
} from "./styles"
import { ShoppingCartContext } from "../../../contexts/ShoppingCart/ShoppingCart.context"

const baseUrlImg = process.env.REACT_APP_BASE_URL_IMG

type CardProps = {
  group: string
  product: Product
}

export function Card({ group, product }: CardProps) {
  const { products, updateProducts } = useContext(ShoppingCartContext)

  return (
    <CardContainer className={product.disabled ? "disabled" : ""}>
      {product.img ? (
        <img src={product.img} alt={"Imagem ilustrativa " + product.name} />
      ) : null}

      <h3>{product.name}</h3>
      {product.items.length > 0 && (
        <Nav>
          <List>
            {product.items.map((item, index) => (
              <ListItem key={item + index}>
                <img
                  src={`${baseUrlImg}${item}.jpg`}
                  alt={"Imagem ilustrativa do produto " + item}
                />
                {item.includes("_")
                  ? (item[0].toUpperCase() + item.slice(1)).replace("_", " ")
                  : item[0].toUpperCase() + item.slice(1)}
              </ListItem>
            ))}
          </List>
        </Nav>
      )}
      {product.prices.length > 0 && (
        <Prices>
          {product.prices.map((price, index) => (
            <Label key={price.key + index}>
              <input
                type="radio"
                name={group + product.name}
                value={product.price}
                onChange={() => {
                  const newProducts = products.map((productGroup) => {
                    let newProductGroup = productGroup
                    if (productGroup.name === group) {
                      newProductGroup.items = productGroup.items.map((item) => {
                        let newItem = item
                        if (item.id === product.id) {
                          newItem = {
                            ...item,
                            price: +price.value
                          }
                        }
                        return newItem
                      })
                    }
                    return productGroup
                  })

                  updateProducts(newProducts)
                }}
                checked={product.price === +price.value}
              />
              <Price>
                <span className="material-icons-outlined">local_atm</span>
                {price.key
                  ? `${
                      price.key +
                      " - " +
                      (+price.value).toLocaleString("pt-BR", {
                        style: "currency",
                        currency: "BRL"
                      })
                    }`
                  : (+price.value).toLocaleString("pt-BR", {
                      style: "currency",
                      currency: "BRL"
                    })}
              </Price>
            </Label>
          ))}
        </Prices>
      )}

      <CardButton
        onClick={() => {
          const newProducts = products.map((productGroup) => {
            let newProductGroup = productGroup
            if (productGroup.name === group) {
              newProductGroup.items = productGroup.items.map((item) => {
                let newItem = item
                if (item.id === product.id) {
                  newItem = {
                    ...item,
                    disabled: true,
                    quantity: "1",
                    price: product.price
                  }
                }
                return newItem
              })
            }
            return productGroup
          })

          updateProducts(newProducts)
        }}
        disabled={product.disabled}
      >
        <span className="material-icons-outlined">shopping_cart</span>
        Selecionar
      </CardButton>
    </CardContainer>
  )
}
