import { useContext } from "react"
import { ShoppingCartContext } from "../../../contexts/ShoppingCart/ShoppingCart.context"
import { HeaderContainer } from "./styles"

const REACT_APP_LOGOURL = process.env.REACT_APP_LOGOURL

type HeaderProps = {
  setShowShoppingCartDialog: React.Dispatch<React.SetStateAction<boolean>>
}

export function Header({ setShowShoppingCartDialog }: HeaderProps) {
  const { products } = useContext(ShoppingCartContext)

  return (
    <HeaderContainer>
      <img src={REACT_APP_LOGOURL} alt="Logotipo do Vicent's Burger" />

      <button
        className="shopping-cart"
        onClick={() => setShowShoppingCartDialog(true)}
      >
        <span
          className="material-icons-outlined"
          data-amounts={
            products?.reduce((accProductGroup, productGroup) => {
              accProductGroup += productGroup.items.reduce(
                (accProduct, product) => {
                  if (product.disabled) accProduct += 1
                  return accProduct
                },
                0
              )
              return accProductGroup
            }, 0) || 0
          }
        >
          shopping_cart
        </span>
        Carrinho de compras
      </button>
    </HeaderContainer>
  )
}
