import styled from "styled-components"

export const Button = styled.button`
  display: flex;
  margin: 0 16px;
  align-items: center;
  justify-content: center;
  gap: 8px;
  color: #ffffff;
  border: none;
  padding: 12px 16px;
  border-radius: 4px;
  width: 100%;
  font-size: 18px;
  cursor: pointer;
  transition: background-color 250ms ease;
  margin-bottom: 24px;

  &:not(:disabled) {
    background-color: #dc143c;
    &:hover {
      background-color: #ee284f;
    }
  }

  &:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
  }
`

export const InputWrapper = styled.div`
  display: flex;
  flex-direction: column;
  border: 1px solid #44444488;
  border-radius: 4px;
  padding: 2px 8px 4px;
  gap: 4px;

  &:has(:focus) {
    border-color: #444444;
  }

  span {
    color: red;
  }

  label {
    font-size: 14px;
  }
  input {
    border: none;
    outline: none;
    background-color: transparent;
    font-size: 16px;
    width: 100%;
  }
`
export const InputBox = styled.div`
  width: 100%;

  &:has(:invalid) {
    span.error {
      display: block;
    }
  }
  span.error {
    padding-inline: 8px;
    color: red;
    font-size: 12px;
    display: none;
  }

  &.mb-2 {
    margin-bottom: 16px;
  }
`
export const InputGroup = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
  margin-bottom: 16px;

  [role="radiogroup"] {
    display: flex;
    label {
      flex: 0 !important;
    }
  }

  label.MuiFormControlLabel-root {
    flex: 1 1 100%;

    span.MuiFormControlLabel-label {
      font-size: 14px !important;
    }
  }
`

export const RadioGroupWrapper = styled.div``
export const RadioGroup = styled.div`
  display: flex;
  align-items: center;
  gap: 16px;
  flex-wrap: wrap;
`
export const Radio = styled.label`
  display: flex;
  align-items: center;
  gap: 12px;
  cursor: pointer;

  input {
    scale: 1.5;
  }

  span.label {
    display: flex;
    align-items: center;
    gap: 2px;
  }
`
