import { DialogActions, DialogContent, DialogProps } from "@mui/material"
import { Dialog } from "../Dialog"
import { Button, Info } from "./styles"

type NoBondDialogProps = DialogProps & {
  dispatch?: () => void
}

export function NoBondDialog({ open, onClose, dispatch }: NoBondDialogProps) {
  return (
    <Dialog title="Sem vínculo" fullWidth={true} maxWidth={"sm"} open={open}>
      <DialogContent>
        <Info>
          Este site não é afiliado ao Facebook ou a qualquer entidade do
          Facebook. Depois que você sair do Facebook, a responsabilidade não é
          deles e sim do nosso site. Fazemos todos os esforços para indicar
          claramente e mostrar todas as provas do produto e usamos. Nós não
          compartilhamos o seu e-mail ou qualquer informação para terceiros.
          Jamais fazemos nenhum tipo de spam. Se você tiver alguma dúvida,
          sinta-se à vontade para usar o link de contato e falar conosco em
          horário comercial de Segunda a Sextas das 18:00 ás 00:00. Lemos e
          respondemos todas as mensagens por ordem de chegada.
        </Info>
      </DialogContent>
      <DialogActions>
        <Button
          onClick={() => {
            dispatch!()
            localStorage.setItem("noBond", "true")
          }}
        >
          ok
        </Button>
      </DialogActions>
    </Dialog>
  )
}
