import styled from "styled-components"

export const WrapperSocialMedia = styled.div`
  position: fixed;
  right: 0;
  top: 50%;
  translate: 0 -50%;
  background-color: rgb(47, 47, 47);
  max-width: 42px;
  width: 100%;
  min-height: 150px;
  border-top-left-radius: 16px;
  border-bottom-left-radius: 16px;

  ul {
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 16px;
    min-height: inherit;

    li {
      line-height: 0;
      color: #ffffff;

      a {
        width: 28px;
        height: 28px;
        display: block;
        position: relative;
        color: inherit;

        > * {
          position: absolute;
          left: 50%;
          top: 50%;
          translate: -50% -50%;
          font-size: 24px;
        }
      }
    }
  }
`
