import styled from "styled-components"

export const CardContainer = styled.div`
  min-width: 290px;
  max-width: 350px;
  min-height: 350px;
  border-radius: 16px;
  background-color: #fff;
  padding-bottom: 24px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  transition: all 450ms ease-in-out;

  &.disabled,
  &.disabled button,
  &.disabled label,
  &.disabled input[type="radio"] {
    filter: grayscale(1);
    cursor: not-allowed;
  }

  img {
    width: 100%;
    object-fit: cover;
    max-height: 196px;
  }

  h3 {
    text-align: center;
    margin-top: 24px;
  }
`

export const Nav = styled.nav`
  flex: 1;
`
export const List = styled.ul`
  padding-inline: 24px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 6px;
  margin-top: 24px;
  list-style: none;
`
export const ListItem = styled.li`
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 16px;
  font-weight: bold;
  color: #2f2f2f;
  flex: 1 1 calc(50% - 8px);

  img {
    max-width: 38px;
    aspect-ratio: 1;
    border-radius: 50%;
    object-fit: cover;
  }
`

export const Label = styled.label`
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  flex: 1 1 auto;
  max-height: 36px;
`

export const Prices = styled.div`
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 12px;
  margin-block: 16px;
  padding-inline: 24px;
`
export const Price = styled.span`
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  margin-bottom: 16px;
  font-weight: bold;
  font-size: 14px;
  margin-top: 24px;

  [class*="material-icons"] {
    background-color: green;
    color: #fff;
    line-height: 20px;
    font-size: 24px;
  }
`

export const CardButton = styled.button`
  display: flex;
  margin: 0 auto;
  align-items: center;
  justify-content: center;
  gap: 8px;
  background-color: #2f2f2f;
  color: #ffffff;
  border: none;
  padding: 12px 16px;
  border-radius: 4px;
  width: 90%;
  font-size: 18px;
  cursor: pointer;
  transition: background-color 250ms ease;

  &:hover:not(:disabled) {
    background-color: #525252;
  }
`
