import styled from "styled-components"

export const HeaderContainer = styled.header`
  display: flex;
  min-height: 75px;
  box-shadow: 2px 2px 5px rgba(255, 255, 255, 0.5);
  align-items: center;
  justify-content: space-between;
  background-color: #fff;
  padding: 12px 16px;
  position: sticky;
  left: 0;
  top: 0;
  right: 0;
  z-index: 99;

  img {
    max-height: 50px;
  }
  button {
    background-color: transparent;
    border: none;
    display: flex;
    align-items: center;
    gap: 12px;
    font-size: 16px;
    cursor: pointer;

    [class*="material-icons"] {
      position: relative;
      border-radius: 50%;
      padding: 4px;
      aspect-ratio: 1;
      transition: all 250ms ease;
      &::after {
        content: attr(data-amounts);
        position: absolute;
        right: -2px;
        top: -2px;
        background-color: red;
        color: #fff;
        padding: 4px;
        width: 100%;
        max-width: 8px;
        max-height: 8px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 10px;
        font-family: Arial, Helvetica, sans-serif;
      }
    }

    &:active,
    &:hover {
      [class*="material-icons"] {
        background-color: #acacac;
        color: #fff;
        pointer-events: none;
      }
    }
  }
`
